import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';

// import Background from './components/background/Background';
import Welcome from './components/welcome/Welcome';
// import Navigation from './components/navigation/Navigation';
import Safearea from './components/safearea/Safearea';

// import { TRANSIT_TYPES_ARR } from './components/world/constants';

import './App.css';
export default class App extends Component {
  constructor(props) {
    super(props);

    this.toggleMenu = this.toggleMenu.bind(this);
    this.closeMenu = this.closeMenu.bind(this);
  }

  state = {
    Background: null,
    Core: null,
    Navigation: null,
    isMenuOpen: false,
  };

  actions = {
    toggleMenu: this.toggleMenu.bind(this),
  };

  toggleMenu() {
    this.setState({
      isMenuOpen: !this.state.isMenuOpen,
    });
  }

  closeMenu() {
    this.coreComponent.toggle(true);
    this.setState({
      isMenuOpen: false,
    });
  }

  componentDidMount() {
    Promise.all([
      import('./components/background/Background'),
      import('./components/navigation/Navigation'),
      import('./components/core/Core'),
    ]).then(modules => {
      this.setState({
        Background: modules[0].default,
        Navigation: modules[1].default,
        Core: modules[2].default,
      });
    });
  }

  render() {
    const { Navigation, Background, Core } = this.state;
    const scale = 0.8;
    const offset = {
      x: Math.floor(window.innerWidth * 0.5 * (1 / scale)),
      y: Math.floor(
        (window.innerHeight - window.innerHeight * scale) * 0.5 * (1 / scale)
      ),
    };

    const containerStyle = {
      transform: this.state.isMenuOpen
        ? `scale3d(${scale}, ${scale}, 1) translate3d(${offset.x}px, ${
            offset.y
          }px, 0)`
        : '',
      // opacity: this.state.isMenuOpen ? 0.5 : 1
    };

    return (
      <div className="app" ref={el => (this.containerEl = el)}>
        <Safearea />
        {Background ? <Background isActive={this.state.isMenuOpen} /> : null}
        {Navigation ? (
          <Navigation
            isOpen={this.state.isMenuOpen}
            offset={offset}
            onClose={this.closeMenu}
          />
        ) : null}
        <div className="appContainer" style={containerStyle}>
          <Switch>
            <Route exact path="/" component={Welcome} />
            <Route
              path="/its"
              render={props =>
                Core ? (
                  <Core
                    {...props}
                    isRunning={!this.state.isMenuOpen}
                    actions={this.actions}
                    ref={child => (this.coreComponent = child)}
                  />
                ) : null
              }
            />
            <Route
              path="/bus"
              render={props =>
                Core ? (
                  <Core
                    {...props}
                    isRunning={!this.state.isMenuOpen}
                    section="BUS"
                    actions={this.actions}
                    ref={child => (this.coreComponent = child)}
                  />
                ) : null
              }
            />
            <Route
              path="/tram"
              render={props =>
                Core ? (
                  <Core
                    {...props}
                    isRunning={!this.state.isMenuOpen}
                    section="TRAM"
                    actions={this.actions}
                    ref={child => (this.coreComponent = child)}
                  />
                ) : null
              }
            />
            <Route
              path="/train"
              render={props =>
                Core ? (
                  <Core
                    {...props}
                    isRunning={!this.state.isMenuOpen}
                    section="TRAIN"
                    actions={this.actions}
                    ref={child => (this.coreComponent = child)}
                  />
                ) : null
              }
            />
            <Route component={Welcome} />
          </Switch>
        </div>
      </div>
    );

  }
}



